*{
    margin: 0; 
    padding: 0;
    box-sizing: border-box;
  }
 
html, body {
    width: 100%;
    height: 100%;
}

header, nav, footer{
    width: 100%;
    height: 10%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;     
}

h1, h2{
    width: 100%;
    text-align: center;
}

nav{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center; 
    
    #languageBtn{
        width: 50px;
        height: 50px;
        border-radius: 10px;
        border: 1px solid #0f52ba;
        outline: none;
        font-size: 1.6em;
        font-weight: bold;
        background-color: #0f52ba;
        color: #fff;
    }

    #languageBtn:active{
        background-color: #72a5f4;
        border: 4px solid #0f52ba;
        color: #000;
    }
}

#mainContainer{
    width: 90%;
    height: 70%;
    margin: 0 auto;
    overflow: auto;  
}